.header {
    background: #FCFCFD;
    position: fixed;
    z-index: 2;
    top: 0;
    left: 0;
    right: 0;
    box-sizing: border-box;



    .header-inner {
            display: flex;
            align-items: center;
            justify-content: space-between;
            padding-top: 24px;
            padding-bottom: 24px;
            box-sizing: border-box;






            .header-left {
                display: flex;
                align-items: center;

            }

            .header-right {
                display: flex;
                align-items: center;
                box-sizing: border-box;
            }

            .line {
                width: 2px;
                height: 40px;
                background: #E6E8EC;
                border-radius: 2px;
                margin-right: 32px;

            }

            .header-left__logo {
                width: 126px;
                height: 32px;
                margin-right: 32px;
            }

            

            .header-left__links {
                font-family: 'DM Sans';
                font-style: normal;
                font-weight: 700;
                font-size: 14px;
                line-height: 16px;
                color: #23262F;
                margin-right: 32px;


                
                
            }

            .header-left__links:last-child {
              
                margin-right: 0;


                
                
            }

           

            

            .header-right-btn {
                border: none;
                outline: none;
                background: transparent;
                cursor: pointer;
                margin-right: 24px;

                .header-right-btn__bell {
                    
                    width: 19px;
                    height: 19px;
                }
            }

            .header-right-label {
                display: flex;
                align-items: center;
                justify-content: space-between;
                width: 256px;
                border: 2px solid #E6E8EC;
                border-radius: 8px;
                padding: 10px 16px;
                margin-right: 36px;


                .header-right__searcher {
                    
                    background-color: transparent;
                    outline: none;
                    border: none;
                    font-family: 'Poppins';
                    font-style: normal;
                    font-weight: 400;
                    font-size: 12px;
                    line-height: 20px;
                    display: flex;
                    align-items: center;
                    color: #777E91;
                }

                .header-right__icon {
                    width: 16px;
                    height: 16px;
                }
            }

            .header-right__btn1 {
                font-family: 'DM Sans';
                font-style: normal;
                font-weight: 700;
                font-size: 16px;
                line-height: 16px;
                text-align: center;
                color: #FCFCFD;
                padding: 16px 20px;
                background: #3772FF;
                border-radius: 90px;
                outline: none;
                cursor: pointer;
                border: none;
                margin-right: 8px;
                transition: all ease .5s;
                border: 2px solid transparent;
            }
            

            .header-right__btn1:hover {
                color: #23262F;
                border: 2px solid #E6E8EC;
                background-color: transparent;
            }

            .header-right__btn2-other {
                display: flex;
                align-items: center;
                justify-content: space-between;
                font-family: 'DM Sans';
                font-style: normal;
                font-weight: 700;
                font-size: 16px;
                line-height: 16px;
                text-align: center;
                color: #23262F;
                border: 2px solid #E6E8EC;
                border-radius: 90px;
                padding-top: 8px;
                padding-bottom: 8px;
                outline: none;
                cursor: pointer;
                background-color: transparent;
                box-sizing: border-box;
                
            }


            .header-right__img {
                border-radius: 50%;
                margin-right: 12px;
            }

            .header-right__text {
                font-family: 'DM Sans';
                font-style: normal;
                font-weight: 700;
                font-size: 14px;
                line-height: 16px;
                text-align: center;
                color: #23262F;

                span {
                    color: #45B36B;
                }
            }

            
    }

    

    .popover-wrapper {
        position: absolute;
        right: 50px;
        top: 75px;
        background: #FCFCFD;
        box-shadow: 0px 24px 24px -16px rgba(15, 15, 15, 0.2);
        border-radius: 16px;
        display: none;


        .popover-inner {
            padding: 32px 16px;


            .popover-balance {
                background: #FCFCFD;
                box-shadow: 0px 24px 24px -16px rgba(15, 15, 15, 0.2);
                border-radius: 16px;
                margin-bottom: 24px;

                .popover-balance-inner {
                        padding: 16px;
                }

                .popover-balance-infoWrap {
                    display: flex;
                    align-items: center;
                    margin-bottom: 10px;
                }
            }

            .popover__img {
                width: 40px;
                height: 40px;
                margin-right: 16px;
            }


            .popover__title2 {
                font-family: 'Poppins';
                font-style: normal;
                font-weight: 400;
                font-size: 12px;
                line-height: 20px;
                color: #777E91;
            }

            .popover__title3 {
                font-family: 'Poppins';
                font-style: normal;
                font-weight: 600;
                font-size: 24px;
                line-height: 32px;
                color: #141416;
            }


            .popover___btn {
                width: 100%;
                font-family: 'DM Sans';
                font-style: normal;
                font-weight: 700;
                font-size: 14px;
                line-height: 16px;
                text-align: center;
                color: #23262F;
                padding: 10px 10px;
                border: 2px solid #E6E8EC;
                border-radius: 90px;
                outline: none;
                cursor: pointer;
                background-color: transparent;
            }

            .popover__list-items {
                width: 100%;
                font-family: 'DM Sans';
                font-style: normal;
                font-weight: 700;
                font-size: 14px;
                line-height: 16px;
                color: #777E91;
                border: none;
                outline: none;
                background-color: transparent;
                display: flex;
                align-items: center;
                cursor: pointer;
                padding-bottom: 14px;
                padding-top: 14px;
                border-bottom: 1px solid #E6E8EC;

            }
            
           .popover__list-items:first-child {
               padding-top: 0;
           }

           .popover__list-items:last-child {
            padding-bottom: 0;
            border: none;
        }
 

            .popover__list-items.list-1::before {
               content: url('../../assets//images/user.svg');
               width: 20px;
                height: 20px;
                margin-right: 5px;
            }

            .popover__list-items.list-2::before {
                content: url('../../assets//images/gal.svg');
                width: 20px;
                 height: 20px;
                 margin-right: 5px;
             }


             .popover__list-items.list-3::before {
                content: url('../../assets//images/lamp.svg');
                width: 20px;
                 height: 20px;
                 margin-right: 5px;

             }

             .popover__list-items.list-3 {
                 padding-bottom: 0;
                 padding-top: 0;

             }

           
             .popover__list-items.list-4::before {
                content: url('../../assets//images/exit.svg');
                width: 20px;
                 height: 20px;
                 margin-right: 5px;
             }
        }


        .add-wrap {
            display: flex;
            justify-content: center;
        }

       
    }
    
}


.dropdown:hover  {


    .popover-wrapper {
        display: block;
    }
    
}

.them {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-top: 14px;
    padding-bottom: 14px;
    border-bottom: 1px solid #E6E8EC;
}


